/* BEGIN Light */

@font-face {
  font-family: 'Founders Grotesk';
  src: url("~fonts/founders-grotesk/FoundersGroteskWeb-Light.eot?v=1.0.0");
  src: url("~fonts/founders-grotesk/FoundersGroteskWeb-Light.eot?#iefix&v=1.0.0") format("embedded-opentype"), url("~fonts/founders-grotesk/FoundersGroteskWeb-Light.woff2?v=1.0.0") format("woff2"), url("~fonts/founders-grotesk/FoundersGroteskWeb-Light.woff?v=1.0.0") format("woff");
  font-weight: 300;
  font-style: normal;
}

/* END Light */

body,
h1,
h2,
h3,
h4,
h5,
h6,
a,
p {
  font-family: "Founders Grotesk", sans-serif;
  font-weight: 300;
  font-size: 22px;
  letter-spacing: .05em;
  line-height: 27px;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin-bottom: 27px;
}

a:hover {
  font-weight: 300;
  border-bottom: 2px solid #ce4f02 !important;
  border-image: none;
}

#amphoraOutline {
  display: block !important;
}

#amphoraOutline .st2 {
  fill: none;
  stroke-width: 2;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-miterlimit: 10;
}

#amphoraOutline .st3 {
  fill: none;
  stroke-width: 2;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-miterlimit: 10;
}

#amphoraOutline .st4 {
  fill: none;
  stroke-width: 2;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-miterlimit: 10;
}

#amphoraOutline .st5 {
  fill: none;
  stroke-width: 2;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-miterlimit: 10;
}

#amphoraOutline .st6 {
  fill: none;
  stroke-width: 2;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-miterlimit: 10;
}

#amphoraOutline .st7 {
  fill: none;
  stroke-width: 2;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-miterlimit: 10;
}

#amphoraOutline .st8 {
  fill: #22252A;
}

#amphoraOutline .st9 {
  fill: none;
  stroke-width: 2;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-miterlimit: 10;
}

#amphoraOutline .st3,
#amphoraOutline .st4,
#amphoraOutline .st5,
#amphoraOutline .st6,
#amphoraOutline .st7,
#amphoraOutline .st9 {
  stroke-dasharray: 1000;
  stroke-dashoffset: 1000;
  animation: dash 5s linear forwards;
}

#amphoraOutline .st3,
#amphoraOutline .st5 {
  animation-delay: .5s;
}

#amphoraOutline .st4,
#amphoraOutline .st6 {
  animation-delay: 2.4s;
}

#amphoraOutline .st7 {
  animation-delay: 2.5s;
}

#amphoraOutline .st9 {
  animation-delay: 3s;
}

#amphoraOutline .st8 {
  animation: eye 1s forwards;
  animation-delay: 2.7s;
  fill-opacity: 0;
}

#amphoraOutline .st2 {
  stroke-dasharray: 1000;
  stroke-dashoffset: 1000;
  animation: dash1 3s linear forwards;
}

@keyframes eye {
  from {
    fill-opacity: 0;
  }

  to {
    fill-opacity: 1;
  }
}

@keyframes dash1 {
  to {
    stroke-dashoffset: 0;
    stroke-dashoffset: 0;
  }
}

@keyframes dash {
  to {
    stroke-dashoffset: 0;
  }
}

#amphoraOutline .stroke--time {
  stroke: #F5F5F5;
}

#amphoraOutline .fill--time {
  fill: #F5F5F5;
}

[data-time=night] #amphoraOutline .stroke--time {
  stroke: #22252A;
}

[data-time=night] #amphoraOutline .fill--time {
  fill: #22252A;
}

.top-bar {
  background: transparent;
  display: flex;
  height: 80px;
  justify-content: center;
  padding: 26px;
  pointer-events: none;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 10;
}

.top-bar .top-bar-center {
  display: inline-block;
}

.footer {
  padding-left: 15px;
  padding-right: 15px;
  position: absolute;
  top: calc(100vh - 80px);
  width: 100vw;
}

.gallery .gallery-item {
  padding-top: 15px;
  padding-bottom: 15px;
}

[data-time="day"] .footer {
  background-color: white;
}

.bottom-bar {
  display: flex;
  justify-content: center;
  padding: 26px;
  width: 100%;
}

.bottom-bar .bottom-bar-center {
  display: inline-block;
}

.bg--time {
  background: white;
  transition: background 5s ease-in-out;
}

[data-time=night].bg--time,
[data-time=night] .bg--time {
  background: #181b20;
}

.home--wrap {
  height: 100vh;
  overflow: hidden;
  background: white;
}

.ampersand-wrap {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  align-items: center;
  display: flex;
  justify-content: center;
  overflow: hidden;
  padding: 80px;
}

.video-wrap {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  padding: 80px;
}

.video-container {
  align-items: center;
  display: flex;
  height: 100%;
  justify-content: center;
  overflow: hidden;
  position: relative;
  width: 100%;
}

.video-container .play-button {
  background-color: #181b20;
  background-image: url("../img/btn--play.svg");
  background-size: 30px 30px;
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 5px;
  height: 50px;
  left: 50%;
  margin-left: -25px;
  margin-top: -25px;
  opacity: 0;
  position: absolute;
  top: 50%;
  width: 50px;
  z-index: 10;
}

.video-container .play-button.open {
  opacity: 1;
}

.show--day,
.show--night {
  visibility: hidden;
}

[data-time=night] .show--night {
  visibility: visible;
}

[data-time=night] .show--day {
  display: none;
  visibility: visible;
}

[data-time=day] .show--day {
  visibility: visible;
}

[data-time=day] .show--night {
  display: none;
  visibility: visible;
}

#intro {
  align-items: center;
  background-color: white;
  bottom: 0;
  display: flex;
  justify-content: center;
  left: 0;
  position: fixed;
  opacity: 0;
  right: 0;
  pointer-events: none;
  top: 0;
  transition: opacity 2s ease-in-out;
  z-index: 5;
}

#intro.open {
  opacity: 1;
  pointer-events: all;
}

[data-time=night] #intro {
  background: #181b20;
}

.animation-wrapper {
  transform: scale(0.75);
}

.animation-frame {
  opacity: 0;
}

.animation-frame.in {
  opacity: 1;
}

#amphoraAnimation svg {
  position: absolute;
  left: -2px;
  top: 0;
  max-height: none;
  height: 392px;
  width: 298px;
}

#amphoraAnimation .animation-frame.in {
  transition: opacity 1500ms ease-in;
}

#contact-overlay {
  align-items: center;
  background: inherit;
  flex-direction: column;
  justify-content: center;
  left: 0;
  overflow-y: auto;
  opacity: 0;
  padding: 0;
  pointer-events: none;
  position: fixed;
  top: 0;
  bottom: 0;
  transition: opacity 2000ms ease-in-out;
  width: 100vw;
  z-index: 10;
}

#contact-overlay.open {
  opacity: 1 !important;
  pointer-events: all;
}

#contact-overlay .info {
  text-align: center;
}

[data-close] {
  background-image: url("~img/btn--close.svg");
  background-position: center;
  background-size: contain;
  border-bottom: 0px;
  height: 48px;
  right: 18px;
  opacity: 0;
  position: fixed;
  top: 18px;
  transition: opacity 1500ms ease-in-out;
  width: 48px;
  z-index: 15;
}

[data-close].open {
  opacity: 1 !important;
}

[data-close]:hover {
  border-bottom: 0px !important;
  text-decoration: none;
}

@media screen and (max-width: 768px) {
  #contact-overlay {
    display: block;
  }

  #contact-overlay .info {
    flex-shrink: 0;
    position: relative;
    margin-bottom: 80px;
  }

  .ampersand-wrap .animation-wrapper {
    transform: scale(0.45);
  }

  .video-wrap {
    padding-left: 50px;
    padding-right: 50px;
  }
}




